.filtersAreaContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    background: #59bdde;

}


.stickyFiltersFixed {
    position: fixed;
    z-index: 101;
    
}

.stickyFiltersDesktop {
    min-width: 765px !important;
    max-width: 765px !important;

    top: 58px;
}

.stickyFiltersMobile {
    width: 100%;
    top: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 12px;
    padding-left: 12px;
}

.StickyFiltersNoFixed {
    border-top-left-radius: 16px !important;
    border-top-right-radius: 16px !important;
    

    

}


.stickyFiltersWarpper {
    background-color: #121c25;
  }



  .stickyFiltersMobile .ui.selection.dropdown {
    min-width: 125px;
    width: 125px;
    min-height: 30px;
    height: 30px;
    line-height: 4px;
  }

  .stickyFiltersMobile .ui.dropdown .menu>.item,
  .stickyFiltersMobile .ui.search  {
    font-size: 12px;

  }



  .stickyFiltersMobile  .ui.selection.dropdown>.delete.icon,
   .stickyFiltersMobile  .ui.selection.dropdown>.dropdown.icon,
    .stickyFiltersMobile  .ui.selection.dropdown>.search.icon {
        line-height: 7px;
    }



    .stickyFiltersDesktop .ui.header {
        margin-top: 16px !important;
    }


.goToTopButton {
    position: absolute;
    right: 0;
    margin-top: 3px !important;
    margin-right: 2px !important;

}

.goToTopButtonHidden {
    display: none !important;
}




.stickyFiltersMobile .tagsDropdown .ui.selection.dropdown .menu {
    
    width: 250px !important;
}

.stickyFiltersMobile .tagsDropdown .ui.search.dropdown .menu {
    max-height: 200px !important;
    
}


.stickyFiltersMobile .tagsDropdown .ui.search.dropdown>.text {
    position: absolute;
    left: 12px;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 90px;
    height: 20px;
    line-height: 10px;
    
}


.stickyFiltersMobile .tagsDropdown>i.icon {
    position: relative;
    top: -9px;
    right: 1px;

}











